// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, } from '../../config';
import CablePlan from './cableplan';
// sections
// hooks
import useAuth from '../../hooks/useAuth';
import CableID from './cableid';
// ----------------------------------------------------------------------
export default function DocsCable() {
  const { themeStretch } = useSettings();

  const {setting, user} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Cable Subscription API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
              This section contains the recommended flow for integrating  Cable Subscription services on the {setting?.general?.app_name} RESTful API.
                           </Typography>

        
                <Markdown children={`<hr> <h3>Available Endpoints</h3><br/> <p>To integrate the ${setting?.general?.app_name} Cable Subscription Payment RESTful API, the endpoints below applies:</p>
                <p><br></p><p>
                <ol> <li>cable</li> <li>iuc</li> <li>cable_plan</li> <li>bypass (boolean)</li> <li>request-id (unique)</li> </ol>
                
                `} />
           <CableID />
           <CablePlan />
           <Markdown children={`<hr> <h3>PURCHASE PRODUCT</h3><br/> <p>Using a POST method,  Cable bundle can with the endpoint below:</p>
                <p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/cable</blockquote> <hr> <h3>Code Sample</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                $paypload = array(
                    'cable' => 1,
                     'iuc' => ${user?.phone},
                     'cable_plan' => 1,
                     'bypass' => false,
                     'request-id' => Cable_12345678900);
                     $ch = curl_init();
                     curl_setopt($ch, CURLOPT_URL, '${HOST_API}/api/cable');
                     curl_setopt($ch, CURLOPT_POST, 1);
                     curl_setopt($ch, CURLOPT_POSTFIELDS, json_encode($paypload));
                     curl_setopt($ch, CURLOPT_RETURNTRANSFER, true);
                     $headers = [
                          "Authorization: Token ${user?.apikey}",
                         'Content-Type: application/json'
                     ];
                     curl_setopt($ch, CURLOPT_HTTPHEADER, $headers);
                     $response = curl_exec($ch);
                     curl_close($ch);
                </pre><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "cabl_name": "DSTV",
                    "request-id": "Cable_1234567890",
                    "amount": "12000",
                    "charges": 6000,
                    "status": "success",
                    "message": "successfully purchase DSTV joli joli ₦12000 to 0701339708866",
                    "iuc": "0701339708866",
                    "oldbal": "110128",
                    "newbal": 92128,
                    "system": "API",
                    "wallet_vending": "wallet",
                    "plan_name": "joli joli"
                }
                </pre>
                
                `} />
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
